.NavigationItem {
  margin: 5px;
  box-sizing: border-box;
  display: flex;
  height: 100%;
  /* align-items: center; */
}

.NavigationItem button {
    color: var(--highlight-color);
    width: 100%;
    font-size: 20px;
    box-sizing:border-box;
    display: block;
    border:0;
    background: transparent;
    text-align: left;
    margin-left: 10px
}

.NavigationItem button:hover,
.NavigationItem button:active,
.NavigationItem button.active {
    color: #1a1a1a
}

@media(min-width: 500px){
  /* desktop devices */
  .NavigationItem {
    margin: 10px;
    display: block;
    height: 100%;
    width: auto;
  }
  
  .NavigationItem button {
      color: #fff;
      text-decoration: none;
      font-size: 14px;
      height: 100%;
      padding: 16px 10px;
      border-bottom: 4px solid transparent;
      background:rgba(0, 35, 67, 0.8)
  }
  
  .NavigationItem button:hover,
  .NavigationItem button:active,
  .NavigationItem button.active {
    background-color: #85f5c2;
    border-bottom: 4px solid #40a4c8;
    color: #002343
  }
}